<template>
  <div id="playfield">
      <header id="header">
        <div id="total-money">
            <h3>
                {{data.totalMoney.toFixed(0)}}
                <img src="../assets/Coin.png"/>
            </h3>
        </div>
      </header>
      <div id="miners">
          <ul>
              <li v-for="(miner, index) in data.miners" class="miner">
                  <img v-bind:src="miner.img" alt="">
                  <div class="miner-info">
                    <p id="miner-level">
                        <img src="../assets/lvl.png"/>
                        {{miner.level}}
                    </p>
                    <p id="miner-products">
                        <img src="../assets/mining.png"/>
                        {{miner.mining.toFixed(1)}}/sec
                    </p>
                    <button v-on:click="upgradeMiner(index)" id="miner-upgrade">Upgrade: {{miner.upgradePrice.toFixed(0)}}</button>
                  </div>
                  <p v-if="miner.error" style="color: red;">Not enough money</p>
              </li>
              <button v-bind:class="{red : data.error}" v-on:click="addMiner()" id="add-miner">
                  <p>Buy new for {{data.defPrice}} </p>
              </button>
          </ul>
      </div>

  </div>
</template>

<script>
    export default {
        name: "chatroom",

        data() {
            return {
                data: {
                   miners: [
                    //    {
                    //        startPrice: 100,
                    //        upgradePrice: null,
                    //        mining: 0,
                    //        level: 0,
                    //        img: 'src/assets/miner.png',
                    //        bought: false,
                    //        error: false
                    //    }
                   ],
                   totalMoney: 300,
                   defPrice: 100,
                   error: false
                }
            }
        },
        methods: {
            upgradeMiner(index) {
                if(this.data.totalMoney >= this.data.miners[index].upgradePrice) {
                    this.data.miners[index].level++;
                    this.data.totalMoney -= this.data.miners[index].upgradePrice;
                    this.data.miners[index].error = false;
                    this.data.miners[index].upgradePrice *= 1.3;
                    this.data.miners[index].mining *= 1.5;
                } else {
                    this.data.miners[index].error = true;
                }
            },
            addMiner() {
                if(this.data.totalMoney >= this.data.defPrice)
                    {
                        this.data.totalMoney -= this.data.defPrice;
                        this.data.miners.push({startPrice: this.data.defPrice, upgradePrice: this.data.defPrice, mining: this.data.defPrice * 0.1, level: 1, img: 'src/assets/miner.png', error: false})
                        this.data.defPrice *= 10;
                        this.data.error = false;
                    } else {
                        this.data.error = true;
                    }

            },
            mineMoney() {
                for(let i = 0; i < this.data.miners.length; i++) {
                    this.data.totalMoney += this.data.miners[i].mining;
                }
            }
        },
        created() {
           setInterval(this.mineMoney, 1000);
        }
    }
</script>

<style>
#header {
    height: 50px;
    background: #c1ac38;
    line-height: 50px;
}
ul {
    margin: 0;
    padding: 10px;
}
body {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    background: #ccb42e;
}
 li {
     list-style: none;
 }
#total-money {
}
#total-money h3 img {
    height: 30px;
    position: absolute;
    top: 8px;
    left: -35px;
}
#total-money h3 {
    display: inline-block;
    font-weight: bold;
    margin: 0;
    position: relative;
    margin-left: 55px;
}
 .miner img {
     height: 100px;
     display: inline-block;
 }
 .miner-info {
     display: inline-block;
     padding-bottom: 20px;
 }
 .miner-info p {
     position: relative;
     font-weight: bold;
     margin: 10px 0;
     margin-left: 25px;
 }
 .miner-info p img {
     height: 20px;
     position: absolute;
     top: -5px;
     left: -25px;
 }
 .miner-info button {
     padding: 5px;
     border-radius: 6px;
     border: none;
     border: 2px #217023 solid;
     background: #38c13c;
     color: #fff;
     font-weight: bold;
     font-size: 15px;
 }
  #add-miner p {
      margin: 0;
  }
  #add-miner.red {
    border: 5px red dotted;
    color: red;
}
 #add-miner {
     border: none;
     background: transparent;
     height: 100px;
     width: 100%;
     line-height: 90px;
     text-align: center;
     color: #fff;
     border: 5px #fff dotted;
     font-size: 30px;
 }
  #add-miner:hover {
      background: rgba(255,255,255, .2);
      cursor: pointer;
  }
  #add-miner:hover p {
      display: none;
  }
  #add-miner:hover:before {
      content: '+';
      color: #fff;
      font-size: 60px;
      font-weight: bold;
      text-align: center;
      line-height: 90px;
      
  }
</style>




